import React, { Component } from "react"
import Joi from "joi-browser"
import Input from "./input"
import Select from "./select"

class Form extends Component {
  state = {
    data: {},
    errors: {}
  }

  validate = () => {
    const options = { abortEarly: false }
    const { error } = Joi.validate(this.state.data, this.schema, options)
    if (!error) return null

    const errors = {} // object defined
    for (let item of error.details) // map array to an object
      errors[item.path[0]] = item.message
    return errors
  }

  // Validate one field
  validateProperty = ({ name, value }) => {
    const obj = { [name]: value }
    const schema = { [name]: this.schema[name] }
    const { error } = Joi.validate(obj, schema)
    return error ? error.details[0].message : null
  }

  handleSubmit = e => {
    e.preventDefault() //prevent page reload

    const errors = this.validate() // get that object  of errors with (const errors) and
    this.setState({ errors: errors || {} }) // render errors msg by updating the state && should be set to an object not null
    if (errors) return

    this.doSubmit()
  }

  handleChange = ({ currentTarget: input }) => {
    const errors = { ...this.state.errors } // clone the errors
    const errorMessage = this.validateProperty(input)
    if (errorMessage) errors[input.name] = errorMessage
    else delete errors[input.name]

    const data = { ...this.state.data }
    data[input.name] = input.value
    this.setState({ data, errors })
  }

  renderButton(label) {
    return (
      <button disabled={this.validate()} className="btn btn-primary">
        {label}
      </button>
    )
  }

  renderSelect(name, label, options, placeholder) {
    const { data, errors } = this.state
    return (
      <Select
        name={name}
        value={data[name]}
        label={label}
        options={options}
        placeholder={placeholder}
        onChange={this.handleChange}
        error={errors[name]}
      />
    )
  }

  renderInput(name, label, placeholder, type = "text") {
    const { data, errors } = this.state
    return (
      <Input
        type={type}
        name={name}
        value={data[name]}
        label={label}
        placeholder={placeholder}
        onChange={this.handleChange}
        error={errors[name]}
      />
    )
  }
}

export default Form



